@import './../../index';

.container {

  min-height: calc(100vh - 70px);
  font-size: 20px;
  background-color: $lightTurquoiseColor;
}

.titleTitle {
  padding-top: 20px;
  text-align: center;
  font-weight: 700;
  font-size: 24px;
  margin-bottom: 30px;
}

.form {
  display: flex;
  flex-direction: column;

}

.info {
  align-self: center;
  width: 60%;
  display: flex;
  flex-direction: column;
}

.titlePersonal {
  font-size: 20px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 30px;
}

.row {
  font-size: 18px;
  display: flex;
  align-items: center;
  //justify-content: space-between;
  //flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 15px;
}


.title {
  text-align: left;
  flex: 1 1 auto;
}

.titleCheckbox {

}

.errorMessage {
  flex: 0 1 150px;
  font-size: 16px;
  color: red;
  margin-bottom: 10px;
  text-align: end;
}

.input {
  height: 40px;
  width: 250px;
  padding: 5px 10px;
  border: 1px solid $lightTurquoiseColor;
  border-radius: 5px;
  overflow: hidden;
  font-size: 16px;
  transition: all 0.5s ease 0s;


  color: $brownColor;
  letter-spacing: 1.3px;

  &:focus {
    border: 1px solid $darkTurquoiseColor;
  }
}

.inputArea {
  resize: none;
  width: 100%;
  height: 150px;
}

.inputCheckBox {
  appearance: none;
  background-color: #fff;
  margin: 0;
  //font: inherit;

  display: flex;
  place-content: center;
  position: relative;
  flex: 0 0 24px;
  height: 24px;
  border-radius: 50%;

  //робимо кружечок вибору
  &::after {
    content: "";
    position: absolute;
    top: 3px;
    left: 3px;
    height: 16px;
    width: 16px;
    background-color: $darkTurquoiseColor;
    border-radius: 50%;
    //спочатку скриваємо кружечки
    transform: scale(0);
    transition: all 0.3s ease 0s;
  }

  &:checked {
    &:after {
      transform: scale(1);
    }
  }
  &:focus {
    border: 1px solid $lightTurquoiseColor;
  }
}


.inputError {
  border: 1px red solid;
}

.rowJob {
  flex-direction: column;
}

.rowContacts {
  padding: 0px 50px 0px 50px;
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}

.titleContacts {
  font-size: 20px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 30px;
}

.contactList {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  column-gap: 30px;
  row-gap: 20px;
}

.itemContact {
  font-size: 18px;
  display: flex;
  align-items: center;
  column-gap: 20px;
  margin-bottom: 15px;
  justify-content: space-between;
}

.button {
  align-self: center;
  padding: 10px 50px;
  background-color: $turquoiseColor;
  margin-bottom: 50px;
  color: white;
  font-weight: 600;
  border-radius: 10px;
  letter-spacing: 1.3px;
  &:active{
    background-color:$darkTurquoiseColor;
  }
}
.infoBlock{
  text-align: center;
  margin-bottom: 20px;
}

