@import './../../../index.scss';

.pagination {
  display: flex;
  justify-content: center;
}

.container {
  display: flex;
  margin-bottom: 20px;
  gap: 15px;
}

.pagesList {
  justify-content: stretch;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.button {
    flex: 0 0 50px;
    font-size: 24px;
    padding: 10px 15px;
    border-radius: 15px;
    background-color: $lightTurquoiseColor;
    border: 2px solid $turquoiseColor;

  @media (any-hover: hover) {
    &:hover {
      background-color: $turquoiseColor;
    }
  }

  &:disabled {
    border: 2px solid $lightTurquoiseColor;
    cursor: inherit;

    &:hover {
      background-color: $lightTurquoiseColor;
    }
  }
}

.pageNumber {
  cursor: pointer;
  padding: 5px 10px;
  font-size: 18px;
  font-weight: 600;
  transition: all 0.3s ease 0s;
  border: 2px solid $lightTurquoiseColor;
  border-radius: 5px;
  overflow: hidden;

}

.pageNumber:hover {
  border: 2px solid $turquoiseColor;

}


.pageNumberSelected {
  background-color: $turquoiseColor;
}
