@import "./../../index";

.preloader {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 150px;
}

.container {
  width: 100%;
  //background-color: #9a9588;
}

.body {

  display: flex;
  column-gap: 15px;



  @media (max-width: $mobile) {
flex-wrap: wrap;
     grid-template-columns: 100%;
    grid-template-areas:
            "profile"
            "about"
            "posts"
            "followers";
  }
}

.columnLeft {
  flex: 1 1 65%;
}

.columnRight {
  flex: 1 1 35%;

}

