@import './../../index';

.container {
  height: calc(100vh - 70px);
  display: flex;
}


.dialogsColumn {
  flex: 0 0 35%;
  //@media (max-width: 992px) {
  //  display: none;
  //}
}

.messagesColumn {
  flex: 0 0 65%;
  background-color: $lightTurquoiseColor;
  overflow: hidden;
  padding: 10px;
  border: 5px solid $darkTurquoiseColor;
  border-top: none;

}

.infoSelect {
  font-size: 20px;
  background-color: $darkTurquoiseColor;
  margin-top: 10px;
  padding: 20px;
  text-align: center;
  border-radius: 10px;
  color: white;
  letter-spacing: 1.2px;
}